@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap);
*{box-sizing:border-box}html,body,h1,h2,p,li,ul{list-style:none;margin:0;padding:0;-webkit-user-select:none;-ms-user-select:none;user-select:none}html{font-family:"Poppins", sans-serif;height:100%;background:linear-gradient(270deg, #69c0de, #d885c8, #5e8de3);background-size:800% 800%;-webkit-animation:gradient 15s ease infinite;animation:gradient 15s ease infinite}@-webkit-keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}.app{text-align:center;margin:auto;width:500px}.app__header{padding:15px 0;color:#3d3d3e}.app__list-header{margin:30px 0 10px;color:#3d3d3e}.app__text{padding:10px 0}.app__form{display:flex;flex-direction:row;grid-gap:10px;gap:10px;justify-content:space-between}.app__input{padding:10px;height:44px;font-family:inherit;outline:none;background-color:#0000;border:1px solid black;color:black;transition:0.3s}.app__input::-webkit-input-placeholder{color:#3d3d3e}.app__input:-ms-input-placeholder{color:#3d3d3e}.app__input::placeholder{color:#3d3d3e}.app__input:hover,.app__input:focus{background-color:rgba(255,204,112,0.709804)}.app__input-label{position:relative}.app__button{padding:10px 30px}.app__error{position:absolute;top:100%;left:0;color:#bd1761cc;font-size:15px}

.item{padding:10px;border:1px solid black;display:flex;flex-direction:column;grid-gap:10px;gap:10px;transition:0.3s}.item__header{overflow-wrap:break-word}.item__text{display:flex;flex-direction:row;grid-gap:10px;gap:10px;justify-content:space-between}.item:hover{background-color:#0002}.item--done{background-color:rgba(255,204,112,0.709804)}

.todo-list{list-style:none;display:flex;flex-direction:column;grid-gap:10px;gap:10px}

